* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-size: cover;
  height: 100vh;
  overflow: hidden;
}

#titlebar {
  padding: 5px;
  text-align: right;
  color: white;
  font-size: 1.2em;
}

#menu {
  position: absolute;
  right: 40px;
  top: 40px;
  width: 400px;
  /* height: 0px; */
  background-color: white;
  text-align: center;
  padding: 20px;
  border-radius: 10px;
  z-index: 3;
  display: none;
}

#menu h3 {
  margin: 10px 0;
}

#images {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
}

.background {
  flex: 0 0 150px;
}

.background img {
  width: 140px;
}

div#time {
  margin: 60pt auto 80pt auto;
  padding: 10pt;
  color: white;
  font-size: 90pt;
  font-weight: bold;
  font-family: "sans-serif";
  text-align: center;
  height: 100pt;
}

#searchcontainer {
  margin: 100px auto 150px auto;
  width: 50%;
  display: flex;
  justify-content: center;
  background-color: white;
  border-radius: 6px;
}

label {
  display: inline-block;
  width: 40px;
  background-color: white;
  text-align: center;
  padding: 2px;
  border: 1px solid gray;
  border-radius: 5px;
}

#reposearch {
  display: inline-block;
  margin: 0 auto;
  width: 90%;
  padding: 2px;
  border: 1px solid gray;
  border-radius: 5px;
}

div#repos {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin: 70px 10px;
  justify-content: flex-start;
}

.image-card {
  background-color: white;
  flex: 0 0 19%;
  border-radius: 10px;
  margin: 0 10px;
  padding: 10px;
}

div#repos li {
  list-style: inside;
}

.image-card h2 {
  margin-top: 5px;
  margin-bottom: 5px;
  text-align: center;
}
